import {ChangeDetectorRef} from '@angular/core';

/*
* This util class created to prevent issue described here - https://github.bmc.com/bmc-ux/adapt-angular/issues/3561
* */
export class SafeCdr {

  static detectChanges(cdr: ChangeDetectorRef): void {
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    if (!(cdr as any).destroyed) {
      cdr.detectChanges();
    }
  }

  static markForCheck(cdr: ChangeDetectorRef): void {
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    if (!(cdr as any).destroyed) {
      cdr.markForCheck();
    }
  }

}
